body {
  height: 100vh;
  margin: 0;
}

.stop-watch {
  box-sizing: border-box;
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 50px auto;
  display: flex;
  position: relative;
}

.circle {
  z-index: 2;
  background-color: #bc8517;
  border-radius: 50%;
  width: 6vmin;
  height: 6vmin;
  position: absolute;
}

.long {
  z-index: 0;
  transform-origin: 0 0;
  background-color: green;
  width: 1vmin;
  height: 40vmin;
  animation: 5s steps(60, end) infinite clock;
  position: absolute;
  top: 50%;
}

.short {
  z-index: 1;
  transform-origin: 0 0;
  background-color: #00f;
  width: 2vmin;
  height: 20vmin;
  animation: 60s steps(60, end) infinite clock;
  position: absolute;
  top: 50%;
}

@keyframes clock {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

